import React, { Component } from 'react';
import file from './0.csv';
import './App.css';

const ID_COURS = 0,
NUM_COURS = 1,
PROF_NOM = 2,
GPE_NOM = 3,
GPE_PROMO = 4,
MODULE = 5,
JOUR = 6,
HEURE = 7,
ROOM = 8,
ROOM_TYPE = 9,
COLOR_BG = 10,
COLOR_TXT = 11;

const GROUPES = ["1A", "1B", "2A", "2B", "3A", "3B", "4A", "4B"];
const GROUPES2 = ["1",     , "2",      , "3",      ,  "4"];

const GROUPES_2A = ["1A", "1B", "2A", "2B", "3A", "3B", "4A", "LP"];

var profs = ["TOUS"];
var modules = ["TOUS"];
var salles = ["TOUTES"];

var edtData = [];

class ScheduleCase extends Component {
  constructor(props) {
    super(props);

    this.updateData = this.updateData.bind(this);
    this.updateData();
  }

  updateData() {
    var data = this.props.data;

    if(this.props.debug)
      console.log(this.props.debug)

    for(let i = 0; i < data.length; i++) {
      for(let j = 0; j < data[i].length; j++) {
        let current = data[i][j];

        current.columnStart = j+1;      // Pas forcément : Imaginons le groupe 4B, si les seuls filtres sont [0, 7],
                                        //                 ce groupe appraîtra en colonne 2 et non pas 7
        current.visible = this.props.filter.groups[i].includes(j);
        let c = Math.max(this.props.filter.groups[i].indexOf(j),this.props.filter.groups[i==0?1:0].indexOf(j))+1;
        current.columnStart = c;
        current.columnEnd = c;
        current.row = i+1;
        if(current.row == 2 && this.props.filter.groups[0].length == 0)
          current.row = 1;
        if(j == 0)
          continue;

        if(current.profNom && current.visible && current.profNom == data[i][j-1].profNom) {
          current.columnStart = data[i][j-1].visible ? data[i][j-1].columnStart : data[i][j].columnStart;
          data[i][j-1].visible = false;
          current.columnEnd++;    // Bricolage
        }

      }
    }

    /*for(let i = 0; i < data.length; i++) { // Itère à travers les promos (lignes)
      for(let j = data[i].length-1; j >= 0; j--) { // Itère à travers les groupes (colonnes)
        /*if(this.props.filter.groups[i].length == 0 || !this.props.filter.groups[i].includes(j)) {
          filteredData[i][j] = {};
          continue;
        } else if(i == 0 && j == 0){
          console.log(data[i][j]);
        }

        data[i][j].column = j+1;
        data[i][j].row = i+1;

        if(j == data[i].length-1)
          continue;
        if(data[i][j].profNom && data[i][j].profNom == data[i][j+1].profNom) {
          data[i][j].numGroups = data[i][j+1].numGroups+1 || 2;
          //data[i][j+1] = {};
        }
      }
      /*if(this.props.filter.groups[i].length == 0 || !this.props.filter.groups[i].includes(7)) {
        data[i][7] = {};
        data[i][7] = {};
      }
    }*/
  }

  componentWillReceiveProps(props) {
    this.props = props;
    this.updateData();
  }

  render() {
    let filter = this.props.filter;
    var groupsAmount = Math.max(this.props.filter.groups[0].length, this.props.filter.groups[1].length);
    return (
      <div className="case" style={{gridTemplateColumns: "repeat("+groupsAmount+", 1fr)"}}>
        {this.props.data.map((promos, row) => promos.map((groupe, column) => filter.groups[row].includes(column) && groupe.visible ? (
            <div style={{gridRow: groupe.row, gridColumnStart: groupe.columnStart,
            gridColumnEnd: groupe.columnEnd, backgroundColor: groupe.bgColor, color: groupe.txtColor,
            opacity: (filter.module == "TOUS" || filter.module == groupe.module) &&
                      (filter.profNom == "TOUS" || filter.profNom == groupe.profNom) &&
                      (filter.salle == "TOUTES" || filter.salle == groupe.salle) ? 1 : 0.3}} className="subCase">
                <div>
                <span>{groupe.module}</span>
                <span>{groupe.profNom}</span>
                <span>{groupe.salle}</span>
                </div>
            </div>): null /*(<div style={{gridRow: row+1, gridColumn: column+1}} className="subCase"></div>)*/))}
      </div>
    );
  }
}

class Schedule extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    var debug = 1;
    return (
      <div id="schedule">
        {this.props.edt.map(jour => jour.map(heure => <ScheduleCase debug={debug-- == 1} filter={this.props.filter} data={heure}/>))}
      </div>
    );
  }
}

class FilterChooser extends Component{
  constructor(props) {
    super(props);
    this.state = {week: this.props.filter.week};
    this.currentWeekTimeout = undefined;
  }

  groupChosen(promo, group) {
    if(this.props.firstFilter) {
      this.props.filter.groups[promo] = [group];
      this.props.filter.groups[promo==1?0:1] = [];
    }else {
      let index = this.props.filter.groups[promo].indexOf(group);
      if(index >= 0)
        this.props.filter.groups[promo].splice(index, 1);
      else
        this.props.filter.groups[promo].push(group);
        this.props.filter.groups[promo].sort();
    }
    this.props.onFilterChanged(this.props.filter);
  }

  weekChosen(week) {
    this.setState({week: Number.parseInt(week)});
    if(this.currentWeekTimeout) {
      clearTimeout(this.currentWeekTimeout);
    }
    this.currentWeekTimeout = setTimeout(() => {
      this.props.filter.week = this.state.week;
      this.props.onFilterChanged(this.props.filter);
    }, 2000);
  }

  moduleChosen(mod) {
    this.props.filter.module = mod;
    this.props.onFilterChanged(this.props.filter);
  }

  profNomChosen(profNom) {
    this.props.filter.profNom = profNom;
    this.props.onFilterChanged(this.props.filter);
  }

  salleChosen(salle) {
    this.props.filter.salle = salle;
    this.props.onFilterChanged(this.props.filter);
  }

  render() {
    return (
      <div id="filterBox">
        <h3>Filtres : </h3>
        <div id="groups">
          <h4>Groupe : </h4>
          <div>
            <h5>1ere année :</h5>
              <div className="groupChooser">
              {GROUPES.map((gr, index) => <div onClick={() => this.groupChosen(0, index)} className={this.props.filter.groups[0].includes(index) && !this.props.firstFilter ? "selected" : ""}>{gr}</div>)}
              </div>
            </div>
          <div>
            <h5>2nde année :</h5>
            <div className="groupChooser">
              {GROUPES_2A.map((gr, index) => <div onClick={() => this.groupChosen(1, index)} className={this.props.filter.groups[1].includes(index) && !this.props.firstFilter ? "selected" : ""}>{gr}</div>)}
            </div>
          </div>
        </div>
        <div>
          <h4>Semaine : </h4>
          <input onChange={(e) => this.weekChosen(e.target.value)} type="range" min="1" max="59" step="1" value={this.state.week}/>
          <span id="weekLabel">{this.state.week.toLocaleString('fr', {minimumIntegerDigits: 2})}</span>
        </div>
        <div>
          <h4>Professeur : </h4>
          <select onChange={(e) => this.profNomChosen(e.target.value)}>
            {profs.map(nom => <option value={nom}>{nom}</option>)}
          </select>
        </div>
        <div>
          <h4>Salle : </h4>
          <select onChange={(e) => this.salleChosen(e.target.value)}>
            {salles.map(nom => <option value={nom}>{nom}</option>)}
          </select>
        </div>
        <div>
          <h4>Module : </h4>
          <select onChange={(e) => this.moduleChosen(e.target.value)}>
            {modules.map(nom => <option value={nom}>{nom}</option>)}
          </select>
        </div>
      </div>
    );
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    
    fetch(file).then(data => data.text().then(txt => this.onDataLoaded(txt))).catch(error => console.log(error));
    
    this.onFilterChanged = this.onFilterChanged.bind(this);
    this.state = {firstFilter: true,
                  edtData: [],
                  filter: {
                    week: 7,
                    groups: [[0, 1, 2, 3, 4, 5, 6, 7],
                            [0, 1, 2, 3, 4, 5, 6, 7]],
                    module: "TOUS",
                    profNom: "TOUS",
                    salle: "TOUTES"
                  }
                 };

    /*
    Structuration : 
    Tableau a 5 colonnes (jours), 6 lignes (heures)  /!\ Colonnes avant lignes dans la structure pour que
    ce soit plus simple de récupérer qu'une colonne
    (cf. Mobiles)
    Chaque case est composée d'un tableau a 2 lignes (promo), 8 colonnes (groupes)
    Chaque case est composée de module, prof_nom, salle, bg_color, txt_color
    */
  }
  
  onDataLoaded(data) {
    var lines = data.split("\n");
    var info = lines.shift();
    var tbl = [];
    for(let line of lines)
    tbl.push(line.split(","));
    tbl.pop();
    
    for(let i = 0; i < 6; i++) {    // Creation heures
      edtData[i] = [];
      for(let j = 0; j < 5; j++) {    // Creation jours
        edtData[i][j] = [];
        for(let k = 0; k < 2; k++) {    // Creation promos
          edtData[i][j][k] = [];
          for(let l = 0; l < 8; l++) {    // Creation groupes
            edtData[i][j][k][l] = [];
          }
        }
      }
    }
    
    for(let i = 0; i < tbl.length; i++) {
      let jour = tbl[i][JOUR];
      let heure = tbl[i][HEURE];
      let promo = Number.parseInt(tbl[i][GPE_PROMO].replace("INFO", ""))-1;
      let groups = [];
      
      if(tbl[i][GPE_NOM] == "LP"){
        promo = 1;
        groups[0] = 7;
      }else if(tbl[i][GPE_NOM] == "CE") {
        groups = [0, 1, 2, 3, 4, 5, 6];
        if(promo == 0)
          groups.push(7);
      } else if(tbl[i][GPE_NOM].length == 2) {
        groups[0] = GROUPES.indexOf(tbl[i][GPE_NOM]);
      } else {
        let grs = tbl[i][GPE_NOM].split("");
        for(let gr of grs) {
          let index = GROUPES2.indexOf(gr);
          groups.push(index);
          if(promo == 0 || (promo == 1 && gr != "4"))
            groups.push(index+1);
        }
      }
      for(let group of groups) {
        edtData[heure][jour][promo][group] = {module: tbl[i][MODULE],
          profNom: tbl[i][PROF_NOM],
          salle: tbl[i][ROOM],
          bgColor: tbl[i][COLOR_BG],
          txtColor: tbl[i][COLOR_TXT]};

          if(!profs.includes(tbl[i][PROF_NOM]))
            profs.push(tbl[i][PROF_NOM]);

          if(!salles.includes(tbl[i][ROOM]))
            salles.push(tbl[i][ROOM]);

          if(!modules.includes(tbl[i][MODULE]))
            modules.push(tbl[i][MODULE]);

        }
      }
      this.setState({edtData: edtData});
    }
    
    onFilterChanged(newFilter) {
      this.setState({filter: newFilter, firstFilter: false});
    }

    render() {
      return (
        <div className="App">
          <h1 id="title">Emploi du temps IUT de Blagnac</h1>
          <FilterChooser firstFilter={this.state.firstFilter} filter={this.state.filter} onFilterChanged={this.onFilterChanged}/>
          <Schedule filter={this.state.filter} edt={this.state.edtData} />
        </div>
        );
      }
    }
    
    export default App;
